
.columns{
    display: flex;
    flex-direction: inline;
    padding-top: 20px;
    color: white;
    margin-top: 25px;
    text-align: center;
    justify-content: center;
}


.firstColumn{
    padding-right: 100px;
}

.firstColumn p{
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    text-align: center;
    font-size: 20px;
}

.secondColumn img{
    margin-bottom: 20px;
    background-color: rgba(44, 43, 43, 0.521);
    padding: 5px;
    border: solid 1px rgba(255, 255, 255, 0.027);
    
}

.firstColumn h1{
    font-size: 40px;
    text-align: center;
}


.proyectos{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    text-align: center;
    overflow: hidden;
}

.titulo h3{
    text-align: center;
    color: white;
    font-size: 30px;
}

.imgbutton{
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
}

button{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: rgba(44, 43, 43, 0.521);
    color: white;
    border: rgba(255, 255, 255, 0.144) 1px solid;
    font-size: 18px;
}

button:hover{
    background-color: black;
    color: white;
    cursor: pointer;
}


.boton{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: rgba(44, 43, 43, 0.521);
    color: white;
    border: rgba(255, 255, 255, 0.144) 1px solid;
    font-size: 17px;
    text-decoration: none;
    margin-top: 10px;
    padding: 4px;
    text-align: center;
}

.boton:hover{
    background-color: black;
    color: white;
    cursor: pointer;
}

.hache{
   border: rgba(88, 85, 85, 0.521) 1px solid;
}

.boton2{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: rgba(44, 43, 43, 0.521);
    color: white;
    border: rgba(255, 255, 255, 0.144) 1px solid;
    font-size: 17px;
    text-decoration: none;
    margin-top: 10px;
    padding: 4px;
}

.boton2:hover{
    background-color: black;
    color: white;
    cursor: pointer;
}


.proyectos img{
    border: solid 2px rgba(255, 255, 255, 0.212);
}


@media screen and (max-width: 800px) {
    .proyectos{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        text-align: center;
        overflow: hidden;
    }
    

    .proyectos div{
        margin-top: 10px;
    }


    .columns{
        width: fit-content;
        
    }

    .firstColumn{
        padding-right: 0px;
    }


    .firstColumn h1 {
        font-size: 30px;
        width: 100%;
    }

    .firstColumn p {
        font-size: 15px;
    }
}